    </div>
    <div style="text-align: center">
      <el-button type="primary" style="width: 80%;margin: 20px 0">预计7个工作日内到账，确认提现</el-button>
    </div>

  </div>

</template>

<script>
  export default {
    data () {
      return {
        value:"选项A",
        radio:"1"
      }
    },
    methods:{
    },
    mounted(){

    }
  }
</script>


